@import (less) "color.less";

.icon-size {
  font-size:6.25em;
}

.title{
    font-size: 1.5em;
    color: black;
    text-align: center;
    word-wrap:break-word;
}

.code-warning{
    font-family: "Arial Bold", Arial;
    font-weight: 700;
    font-style: normal;
    font-size: 16px;
    color: rgb(111, 111, 111);
    text-align: center;
    word-wrap:break-word;
}

.message{
    font-weight: 400;
    font-style: normal;
    color: #333333;
    text-align: center;
    line-height: normal;
    
}
.padding-bottom{
    padding-bottom:20px;
}

.box{
    border-width: 0px;
    background: inherit;
    background-color: rgba(248, 248, 248, 1);
    border: none;
    border-radius: 0px;
    box-shadow: none;
    padding:20px;
}
.button-row {
  .padding-bottom
}

.feedback-face-size{
  font-size:12em;
}

.success{
  color: @green;
  .feedback-face-size;
}

.warning{
  color: @orange;
  .feedback-face-size;
}

.error{
  color: @red;
  .feedback-face-size;
}



